<template>
  <div>
    <el-container>
      <!-- 侧边栏 -->
      <el-aside style="min-height: 100vh;width:150px;background-color: #333;white-space: normal;">
        <div style="height: 60px; color: white; display: flex; align-items: center; justify-content: center">
          <span class="logo-title" v-show="!isCollapse">后台管理系统</span>
        </div>
        <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#333" text-color="rgba(255, 255, 255, 0.65)"
                 active-text-color="#fff" style="border: none;white-space: normal" :default-active="$route.path">
          <el-menu-item index="/home" >
            <i class="el-icon-house"></i>
            <span slot="title">首页</span>
          </el-menu-item>
          <el-menu-item index="/taskCreate" v-if="user.authority==='管理员' || user.authority==='超级管理员'">
            <i class="el-icon-s-claim"></i>
            <span>点检任务管理</span>
          </el-menu-item>
          <el-menu-item index="/deviceList" v-if="user.authority==='管理员' || user.authority==='超级管理员'">
            <i class="el-icon-s-management"></i>
            <span>设备信息管理</span>
          </el-menu-item>
          <el-menu-item index="/standard">
            <i class="el-icon-s-data"></i>
            <span>点检数据中心</span>
          </el-menu-item>
          <el-menu-item index="/user" v-if="user.authority==='管理员' || user.authority==='超级管理员'">
            <i class="el-icon-menu"></i>
            <span>系统管理</span>
          </el-menu-item>
        </el-menu>
        <div style="padding-top: 350px">
          <img src="../assets/烟厂logo.png" height="90" width="150"/>
        </div>
      </el-aside>
      <el-container>
        <!-- 头部区域 -->
        <el-header style="background: white">
          <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-left: 20px">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path:$route.path}">{{ $route.meta.name }}</el-breadcrumb-item>
          </el-breadcrumb>
          <div style="flex: 1; width: 0; display: flex; align-items: center; justify-content: flex-end">
            <el-popover placement="bottom" trigger="hover" style="padding-right: 30px">
              <div ref="qrcode"></div>
              <el-button slot="reference" type="success">下载手机客户端</el-button>
            </el-popover>
            <el-dropdown placement="bottom">
              <div style="display: flex; align-items: center; cursor: default">
                <img :src="user.avatar || 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'" alt="" style="width: 50px; height: 50px; margin: 0 5px;border-radius: 50%">
              </div>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="$router.push('/person')">个人信息</el-dropdown-item>
                <el-dropdown-item @click.native="$router.push('/password')">修改密码</el-dropdown-item>
                <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>
        <!-- 主体区域 -->
        <el-main>
          <router-view @update:user="updateUser">
            <img src="../assets/WechatIMG24.png" width="454" height="67"/>
          </router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import QRCode from 'qrcode'

export default {
  name: 'Main',
  data() {
    return {
      isCollapse: false,
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
    }
  },
  mounted() {
    this.loadNotice();
    this.$nextTick(() => {
      this.generateQRCode();
    });
  },
  methods: {
    generateQRCode() {
      const wPath = window.document.location.href;
      const pathName = this.$route.path;
      const pos = wPath.indexOf(pathName);
      const localhostPath = wPath.substring(0, pos);
      const canvas = document.createElement('canvas');
      QRCode.toCanvas(canvas, localhostPath+'/apk/app.apk', { errorCorrectionLevel: 'H' }, (error) => {
        if (error) console.error(error);
        const qrCodeContainer = this.$refs.qrcode;
        if (qrCodeContainer) {
          qrCodeContainer.innerHTML = '';
          qrCodeContainer.appendChild(canvas);
        } else {
          console.error('QR code container not found');
        }
      });
    },
    loadNotice() {
      this.$request.get('/notice/selectUserData').then(res => {
        this.notices = res.data;
      });
    },
    updateUser(user) {
      this.user = JSON.parse(JSON.stringify(user));
    },
    logout() {
      localStorage.removeItem('honey-user');
      this.$router.push('/login');
    },
  }
}
</script>

<style>
.el-menu--inline {
  background-color: #ecedf1 !important;
}
.el-menu--inline .el-menu-item {
  background-color: #ecedf1 !important;
  padding-left: 49px !important;
}
/*.el-menu-item:hover, .el-submenu__title:hover {*/
/*  color: #54ba9c !important;*/
/*}*/
.el-submenu__title:hover i {
  color: #fff !important;
}
.el-menu-item:hover i {
  color: #fff !important;
}
.el-menu-item.is-active {
  background-color: #54ba9c !important;
  border-radius: 5px !important;
  width: calc(100% - 8px);
  margin-left: 4px;
}
.el-menu-item.is-active i, .el-menu-item.is-active .el-tooltip{
  margin-left: -4px;
}
.el-menu-item {
  height: 40px !important;
  line-height: 40px !important;
}
.el-submenu__title {
  height: 40px !important;
  line-height: 40px !important;
}
.el-submenu .el-menu-item {
  min-width: 0 !important;
}
.el-menu--inline .el-menu-item.is-active {
  padding-left: 45px !important;
}


.el-aside {
  transition: width .3s;
  box-shadow: 2px 0 6px rgba(0,21,41,.35);
}
.logo-title {
  /*margin-left: 5px;*/
  font-size: 20px;
  transition: all .3s;   /* 0.3s */
}
.el-header {
  box-shadow: 2px 0 6px rgba(0,21,41,.35);
  display: flex;
  align-items: center;
}
.el-main{
  padding: 0!important;
}
</style>